.richeditor {
  width: 100%;
  font-size: 14px;
}

.richeditor_toolbar {
  border: 1px solid #9E9E9E;
  border-bottom: none;
  overflow: hidden;
}

.richeditor_toolbarItem {
  display: block;
  float: left;
  height: 24px;
  cursor: pointer;
  box-sizing: border-box;
  margin: 4px 2px;
}
.richeditor_toolbarItem.active > svg {
  fill: #F44336;
}

.richeditor_area {
  height: 400px;
  border: 1px solid #9e9e9e;
  outline: none;
}

.rd_select-btn {
  display: flex;
  border: 2px solid #000;
  width: 90px;
}
.rd_select-btn.rd_select-ft-btn {
  width: 109px;
}
.rd_select-btn .rd_select-btn-text {
  flex: 1;
  text-align: center;
}
.rd_select-btn .rd_select-btn-icon {
  display: inline-block;
  width: 18px;
  text-align: center;
}

.rd_select-menu {
  position: absolute;
  font-size: 14px;
  border: 2px solid #000;
  background: #fff;
}
.rd_select-menu ul, .rd_select-menu li {
  margin: 0;
  padding: 0;
}
.rd_select-menu li {
  list-style: none;
}
.rd_select-menu li {
  padding: 5px 12px;
  cursor: pointer;
}
.rd_select-menu li:hover, .rd_select-menu li.active {
  background: #F44336;
  color: #fff;
}

.rd_color-picker {
  position: absolute;
  padding: 8px;
  background: #fff;
  box-shadow: 0 0 6px rgba(66, 66, 66, 0.5);
}

.rd_hub {
  width: 180px;
  height: 12px;
}

.rd_hub-bar {
  position: relative;
  width: 100%;
  height: 100%;
  margin: 5px 0;
  cursor: pointer;
  background: linear-gradient(to right, red, #ff0080, fuchsia, #8000ff, blue, #0080ff, aqua, #00ff80, lime, #80ff00, yellow, #ff8000, red);
}

.rd_hub-btn {
  position: absolute;
  right: 0;
  background: #fff;
  border-radius: 16px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
  box-sizing: content-box;
  height: 16px;
  width: 16px;
  margin-right: -8px;
  margin-top: -2px;
  cursor: pointer;
}

.rd_box {
  display: flex;
  justify-content: space-between;
}

.rd_color-display {
  width: 27px;
  height: 27px;
  background: red;
  margin-top: 11px;
  border-radius: 27px;
}

.rd_rgb-control {
  margin-top: 10px;
  width: 145px;
}
.rd_rgb-control .rd_rgb-control-list {
  width: 100%;
  display: flex;
  height: 50px;
  align-items: center;
  justify-content: space-between;
}
.rd_rgb-control input {
  width: 32px;
  outline: none;
  padding: 3px 4px;
  border: 2px solid #000;
  font-size: 14px;
}
.rd_rgb-control input:focus {
  border-color: #FF9800;
}
.rd_rgb-control .rd_rgb-control-label {
  text-align: center;
  font-size: 14px;
}

.rd_color-select {
  width: 180px;
  height: 50px;
  padding-top: 10px;
  border-top: 2px solid #000;
  margin-top: 6px;
}
.rd_color-select .rd_color-select-item {
  display: inline-block;
  width: 14px;
  height: 14px;
  margin: 0 3px;
  cursor: pointer;
}
.rd_color-select .rd_color-select-item:hover {
  transform: scale(1.1);
}